body,
#root {
  height: 100%;
  width: 100%;
}
div {
  float: left;
  text-align: center;
}
.App {
  height: 100%;
  width: 100%;
}
.input {
  background-color: #f6f5f5;
  width: 30%;
  height: 100%;
  position: fixed;
  display: inline;
  right: 0;
}
.keywords_input {
  width: 100%;
  height: 5%;
  margin-bottom: 10px;
  background-color: #22497e;
}
.keywords {
  margin: 6px;
  width: 30em;
  height: 2em;
}
.keywords_input button {
  height: 2.4em;
  margin-top: 6px;
}
.list {
  background-color: #f6f5f5;
  width: 100%;
  height: 100%;
}
.list span {
  width: 100%;
  height: 7%;
  display: inline-block;
  padding: 5px;
  text-align: left;
}
#map {
  width: 70%;
  height: 100%;
  position: fixed;
}
.list_content {
  padding-bottom: 24px;
  width: 100%;
  border-bottom: #d5dddd solid 1px;
}
